<template>
  <div class="container">
    <h1 class="title">My Favorite Recipes</h1>
    <div v-if="favoriteRecipes.length" class="favorites-list">
      <RecipePreview
        v-for="recipe in favoriteRecipes"
        :key="recipe.id"
        :recipe="recipe"
      />
    </div>
    <div v-else>
      <p>No favorite recipes found.</p>
    </div>
  </div>
</template>

<script>
import RecipePreview from "../components/RecipePreview.vue"; // Adjust this import based on your component structure
import { fetchFavoriteRecipes } from "../services/user.js"; // Import the function to fetch favorite recipes

export default {
  components: {
    RecipePreview,
  },
  data() {
    return {
      favoriteRecipes: [],
    };
  },
  mounted() {
    this.fetchFavoriteRecipes();
  },
  methods: {
    async fetchFavoriteRecipes() {
      try {
        // Call the function to fetch favorite recipes from the backend
        const response = await fetchFavoriteRecipes();

        // Assuming the response contains an array of favorite recipes
        this.favoriteRecipes = response.data.favoriteRecipes;
      } catch (error) {
        console.error("Error fetching favorite recipes:", error);
      }
    },
  },
};
</script>

